*,*::after,*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  background-color: white;
  color: black;
  min-height: 100vh;
}

ul,ol {
  list-style: none;
}
